import React, {useState, useEffect} from 'react';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Row from 'react-bootstrap/Row';
import * as formik from 'formik';
import * as yup from 'yup';
import axios from 'axios';

function Join() {
    const {Formik} = formik;

    const [zipcode, setZipcode] = useState('');
    const [address, setAddress] = useState('');
    const [detailAddress, setDetailAddress] = useState('');

    useEffect(() => {
        if (window.daum) return;

        const script = document.createElement('script');
        script.src = 'https://t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js';
        script.onload = () => {
            console.log('Daum Postcode script loaded');
        };
        document.body.appendChild(script);
    }, []);

    const schema = yup.object().shape({
        username: yup.string().required(' .'),
        password: yup.string().min(6, '  6  .').required(' .'),
        confirmPassword: yup.string().oneOf([yup.ref('password'), null], '  .').required('  .'),
        firstName: yup.string().required(' .'),
        phone: yup.string()
            .matches(/^(\d{3})-(\d{4})-(\d{4})$/, '   010-1234-5678.')
            .required(' .'),
        zip: yup.string().required(' .'),
        address: yup.string().required(' .'),
        detailAddress: yup.string().required(' .'),
        email: yup.string().email('  .').required(' .'),
        receiveEmail: yup.bool().required('   .').oneOf([true], '   .')
    });

    const handleSearchPostcode = (setFieldValue) => {
        new window.daum.Postcode({
            oncomplete: function (data) {
                setZipcode(data.zonecode);
                setAddress(data.roadAddress);
                setFieldValue('zip', data.zonecode);
                setFieldValue('address', data.roadAddress);
            }
        }).open();
    };

    return (
        <Formik
            validationSchema={schema}
            onSubmit={async (values, { setSubmitting, resetForm }) => {
                console.log('  :', values);
                try {
                    const response = await axios.post('http://localhost:18080/api/join', values, {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    });
                    console.log(' :', response.data);
                    alert(' .');
                    resetForm();
                } catch (error) {
                    console.error(' :', error);
                    alert(' .   .');
                }
                setSubmitting(false);
                console.log(" ", values);
            }}
            initialValues={{
                username: '',
                password: '',
                confirmPassword: '',
                firstName: '',
                phone: '',
                zip: '',
                address: '',
                detailAddress: '',
                email: '',
                receiveEmail: false
            }}
        >
            {({handleSubmit, handleChange, setFieldValue, values, touched, errors, isSubmitting}) => (
                <Form noValidate onSubmit={handleSubmit}>
                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikUsername">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=""
                                name="username"
                                value={values.username}
                                onChange={handleChange}
                                isInvalid={!!errors.username}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.username}
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} md="6" controlId="validationFormikPassword">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="password"
                                placeholder=""
                                name="password"
                                value={values.password}
                                onChange={handleChange}
                                isInvalid={!!errors.password}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.password}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikConfirmPassword">
                            <Form.Label> </Form.Label>
                            <Form.Control
                                type="password"
                                placeholder=" "
                                name="confirmPassword"
                                value={values.confirmPassword}
                                onChange={handleChange}
                                isInvalid={!!errors.confirmPassword}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.confirmPassword}
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} md="6" controlId="validationFormikFirstName">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                name="firstName"
                                value={values.firstName}
                                onChange={handleChange}
                                isInvalid={!!errors.firstName}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.firstName}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikPhone">
                            <Form.Label></Form.Label>
                            <InputGroup>
                                <Form.Control
                                    type="text"
                                    placeholder="010-0000-0000"
                                    name="phone"
                                    value={values.phone}
                                    onChange={handleChange}
                                    isInvalid={!!errors.phone}>

                                </Form.Control>
                            </InputGroup>
                            <Form.Control.Feedback type="invalid">
                                {errors.phone}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikZip">
                            <Form.Label></Form.Label>
                            <InputGroup>
                                <Form.Control
                                    type="text"
                                    placeholder=""
                                    name="zip"
                                    value={zipcode || values.zip}
                                    onChange={handleChange}
                                    isInvalid={!!errors.zip}
                                    readOnly
                                />
                                <Button onClick={() => handleSearchPostcode(setFieldValue)}> </Button>
                            </InputGroup>
                            <Form.Control.Feedback type="invalid">
                                {errors.zip}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="12" controlId="validationFormikAddress">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=" "
                                name="address"
                                value={address || values.address}
                                onChange={handleChange}
                                isInvalid={!!errors.address}
                                readOnly
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.address}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="12" controlId="validationFormikDetailAddress">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=""
                                name="detailAddress"
                                value={detailAddress || values.detailAddress}
                                onChange={handleChange}
                                isInvalid={!!errors.detailAddress}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.detailAddress}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikEmail">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="email"
                                placeholder=""
                                name="email"
                                value={values.email}
                                onChange={handleChange}
                                isInvalid={!!errors.email}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.email}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikReceiveEmail">
                            <Form.Check
                                type="checkbox"
                                name="receiveEmail"
                                label="  "
                                checked={values.receiveEmail}
                                onChange={handleChange}
                                isInvalid={!!errors.receiveEmail}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.receiveEmail}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Button type="submit" disabled={isSubmitting}>
                        {isSubmitting ? " ..." : ""}
                    </Button>

                </Form>
            )}
        </Formik>
    );
}

export default Join;
