import React from 'react';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Row from 'react-bootstrap/Row';
import * as formik from 'formik';
import * as yup from 'yup';
import axios from 'axios';

function StoreJoin() {
    const {Formik} = formik;

    const schema = yup.object().shape({
        storeId: yup.string().required('  .'),
        password: yup.string().min(6, '  6  .').required(' .'),
        confirmPassword: yup.string().oneOf([yup.ref('password'), null], '  .').required('  .'),
        storeName: yup.string().required('  .'),
        phone: yup.string()
            .matches(/^(\d{3})-(\d{4})-(\d{4})$/, '   010-1234-5678.')
            .required(' .'),
        taxid: yup.string()
            .matches(/^(\d{3})-(\d{2})-(\d{5})$|^(\d{3})-(\d{2})-(\d{4})$/, '    000-00-0000  000-00-00000.')
            .required('  .'),
        email: yup.string().email('  .').required(' .')
    });

    return (
        <Formik
            validationSchema={schema}
            onSubmit={async (values, {setSubmitting, resetForm}) => {
                console.log('  :', values);
                try {
                    const response = await axios.post('http://localhost:18080/api/storejoin', values, {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    });
                    console.log(' :', response.data);
                    alert(' .  2~3 .');
                    resetForm();
                } catch (error) {
                    console.error(' :', error);
                    alert(' .   .');
                }
                setSubmitting(false);
                console.log(" ", values);
            }}
            initialValues={{
                storeId: '',
                password: '',
                confirmPassword: '',
                storeName: '',
                phone: '',
                taxid: '',
                file: null,
                email: '',
                storeContact: ''
            }}
        >
            {({handleSubmit, handleChange, setFieldValue, values, touched, errors, isSubmitting}) => (
                <Form noValidate onSubmit={handleSubmit}>
                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikstoreId">
                            <Form.Label> </Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=" "
                                name="storeId"
                                value={values.storeId}
                                onChange={handleChange}
                                isInvalid={!!errors.storeId}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.storeId}
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} md="6" controlId="validationFormikPassword">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="password"
                                placeholder=""
                                name="password"
                                value={values.password}
                                onChange={handleChange}
                                isInvalid={!!errors.password}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.password}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikConfirmPassword">
                            <Form.Label> </Form.Label>
                            <Form.Control
                                type="password"
                                placeholder=" "
                                name="confirmPassword"
                                value={values.confirmPassword}
                                onChange={handleChange}
                                isInvalid={!!errors.confirmPassword}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.confirmPassword}
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} md="6" controlId="validationFormikStoreName">
                            <Form.Label>  ()</Form.Label>
                            <Form.Control
                                type="text"
                                name="storeName"
                                value={values.storeName}
                                onChange={handleChange}
                                isInvalid={!!errors.storeName}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.storeName}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikPhone">
                            <Form.Label></Form.Label>
                            <InputGroup>
                                <Form.Control
                                    type="text"
                                    placeholder="010-0000-0000"
                                    name="phone"
                                    value={values.phone}
                                    onChange={handleChange}
                                    isInvalid={!!errors.phone}>

                                </Form.Control>
                            </InputGroup>
                            <Form.Control.Feedback type="invalid">
                                {errors.phone}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikTaxId">
                            <Form.Label> </Form.Label>
                            <InputGroup>
                                <Form.Control
                                    type="text"
                                    placeholder="000-00-00000"
                                    name="TaxId"
                                    value={values.taxid}
                                    onChange={handleChange}
                                    isInvalid={!!errors.taxid}>

                                </Form.Control>
                            </InputGroup>
                            <Form.Control.Feedback type="invalid">
                                {errors.taxid}
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                    <Form.Group as={Col} md="6" controlId="validationFormikFile">
                        <Form.Label> </Form.Label>
                        <Form.Control
                            type="file"
                            required
                            name="file"
                            onChange={handleChange}
                            isInvalid={!!errors.file}/>
                    </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} md="6" controlId="validationFormikEmail">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="email"
                                placeholder=""
                                name="email"
                                value={values.email}
                                onChange={handleChange}
                                isInvalid={!!errors.email}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errors.email}
                            </Form.Control.Feedback>
                        </Form.Group>
                        
                            <Form.Group as={Col} md="6" controlId="validationFormikContact">
                                <Form.Label> </Form.Label>
                                <Form.Control
                                    type="textarea"
                                    placeholder=" "
                                    name="storeContact"
                                    value={values.storeContact}
                                    onChange={handleChange}
                                    isInvalid={!!errors.storeContact}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.storeContact}
                                </Form.Control.Feedback>
                            </Form.Group>
                    </Row>



                    <Button type="submit" disabled={isSubmitting}>
                        {isSubmitting ? " ..." : ""}
                    </Button>

                </Form>
            )}
        </Formik>
    )
}

export default StoreJoin;