import React, { useState } from "react";
import { useNavigate } from "react-router-dom"; // React Router 

function CustomerService() {
  const navigate = useNavigate(); //  
  const [currentPage, setCurrentPage] = useState(1); //   
  const itemsPerPage = 5; //    

  //  
  const boardData = [
    { no: 10, title: "", author: "", date: "2019-12-17", likes: 0 },
    { no: 9, title: "", author: "", date: "2019-12-16", likes: 0 },
    { no: 8, title: " ", author: "", date: "2019-12-16", likes: 0 },
    { no: 7, title: "", author: "", date: "2018-05-14", likes: 0 },
    { no: 6, title: " ~!", author: "", date: "2018-04-17", likes: 0 },
    { no: 5, title: " ~~", author: "", date: "2018-04-17", likes: 0 },
    { no: 4, title: "  ", author: "", date: "2017-12-06", likes: 0 },
    { no: 3, title: "!", author: "", date: "2017-11-20", likes: 0 },
    { no: 2, title: "!", author: "", date: "2017-11-20", likes: 0 },
    { no: 1, title: "", author: "", date: "2017-11-20", likes: 0 },
  ];

  //    
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = boardData.slice(indexOfFirstItem, indexOfLastItem);

  //   
  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  //   
  const totalPages = Math.ceil(boardData.length / itemsPerPage);
  const pageNumbers = Array.from({ length: totalPages }, (_, i) => i + 1);

  return (
    <div style={{ padding: "20px", fontFamily: "Arial, sans-serif", position: "relative" }}>
      {/*   */}
      <h1 style={{ textAlign: "center" }}></h1>

      {/*   */}
      <table style={{ width: "100%", borderCollapse: "collapse" }}>
        <thead>
          <tr style={{ borderBottom: "2px solid #ccc" }}>
            <th style={{ padding: "10px", textAlign: "center" }}></th>
            <th style={{ padding: "10px", textAlign: "left" }}></th>
            <th style={{ padding: "10px", textAlign: "center" }}></th>
            <th style={{ padding: "10px", textAlign: "center" }}></th>
            <th style={{ padding: "10px", textAlign: "center" }}></th>
          </tr>
        </thead>
        <tbody>
          {currentItems.map((item) => (
            <tr key={item.no} style={{ borderBottom: "1px solid #ddd" }}>
              <td style={{ padding: "10px", textAlign: "center" }}>{item.no}</td>
              <td style={{ padding: "10px" }}>{item.title}</td>
              <td style={{ padding: "10px", textAlign: "center" }}>{item.author}</td>
              <td style={{ padding: "10px", textAlign: "center" }}>{item.date}</td>
              <td style={{ padding: "10px", textAlign: "center" }}>{item.likes}</td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* --  */}
      <div style={{ textAlign: "center", marginTop: "20px" }}>
        {pageNumbers.map((number) => (
          <button
            key={number}
            onClick={() => handlePageChange(number)}
            style={{
              margin: "0 5px",
              padding: "8px 16px",
              backgroundColor: number === currentPage ? "#007BFF" : "#ccc",
              color: "white",
              border: "none",
              borderRadius: "4px",
              cursor: "pointer",
            }}
          >
            {number}
          </button>
        ))}
      </div>

      {/*   (  ) */}
      <button
        onClick={() => navigate("/post")} // "(post url)"  
        style={{
          position: "absolute",
          bottom: "20px",
          right: "20px",
          padding: "10px 20px",
          backgroundColor: "#007BFF",
          color: "white",
          border: "none",
          borderRadius: "5px",
          cursor: "pointer",
        }}
      >
        
      </button>
    </div>
  );
}

export default CustomerService;
